@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.socket-status{text-align:center;padding-top:10px;margin:auto;display:flex;justify-content:space-around;padding-bottom:20px}

.side-bar{right:0;width:25%;top:30vh;display:flex;justify-content:space-between;align-items:flex-start;flex-direction:column}

.sidecard{display:flex;align-items:center;border-radius:4px;height:52px;padding:0 8px;margin-bottom:10px;margin-left:5px}.sidecard__avatar{border-radius:100px;display:flex;align-items:center;justify-content:center;flex:0 0 auto;width:42px;height:42px;margin-right:12px}.sidecard__content{white-space:nowrap;text-overflow:ellipsis;overflow:hidden}.sidecard__content__name{display:flex;justify-content:flex-start;align-items:center}.sidecard__content__sub{margin-top:-2px;white-space:nowrap;text-overflow:ellipsis;overflow:hidden}.sidecard__content__sub__text{display:flex;align-items:center;font-size:12px;line-height:16px;color:#bdbbbb;font-weight:500}.sidecard__content__sub__text>span{margin-left:8px}

.storeitem,.storeitem--text{margin:5px;text-align:center}.storeitem--text{color:white;font-size:16px;margin-bottom:20px}

.store-list{display:flex;float:left;flex-wrap:wrap;flex-direction:column;justify-content:space-evenly;border-radius:10px;width:20%;color:white}@media screen and (max-width: 900px){.store-list{width:unset;float:unset;padding:5vh;display:flex;flex-direction:unset}}

.socket-dynamicdrivers{color:white;background-color:#2f3136}.map{height:80vh;width:100%;display:flex;flex-direction:row}@media screen and (max-width: 900px){.map{width:100%;display:block;height:60vh}}.map>div:nth-child(2){width:80% !important}@media screen and (max-width: 900px){.map>div:nth-child(2){width:100% !important}}


.navbar{padding-bottom:5px}

.header{width:11vh;height:11vh;margin:auto;text-align:center}.header__img{height:70px;width:70px}

.App{height:100vh}

body{font-family:"Roboto", sans-serif;background-color:#36393f}*{box-sizing:border-box;margin:0;padding:0}

