.socket-dynamicdrivers {
  color: white;
  background-color: #2f3136;
}
.map {
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  @media screen and (max-width: 900px) {
    width: 100%;
    display: block;
    height: 60vh;
  }
}
.map > div:nth-child(2) {
  width: 80% !important;
  @media screen and (max-width: 900px) {
    width: 100% !important;
  }
}
